.advantages {
  background-image: url("/img/background-advantages.png");
  background-repeat: no-repeat;
  background-position: 50% 174px;
  background-size: cover;
  position: relative;

  @media (min-width: $tablet-width) {
    background-position: 50% 216px;
  }
}

.advantages__header {
  width: 280px;
  margin: 46px auto 123px auto;

  @media (min-width: $tablet-width) {
    width: 100%;
  }
}

.advantages__question,
.advantages__proposal {
  font-size: 24px;
  line-height: 28.8px;
  font-weight: lighter;
  text-align: center;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    line-height: 54.47px;
  }
}

.advantages__question {
  margin-bottom: 45px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.advantages__main {
  display: flex;
  flex-direction: column;
  margin: 0 20px;

  @media (min-width: $tablet-width) {
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    margin: 0 130px;
  }
}

.advantages__picture {
  height: 320px;
  background-image: url("/img/screen-friend.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;

  @media (min-width: $tablet-width) {
    min-width: 50%;
    min-height: 547px;

    @media (min-resolution: 2dppx) {
      background-image: url("/img/screen-friend@2x.png");
    }
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.advantages__list {
  list-style-type: none;
  padding: 0;
  margin: 0;

  @media (min-width: $tablet-width) {
    width: 50%;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}

.advantages__title {
  font-size: 18px;
  color: $basic-dark;
  text-align: center;
  margin: 0;
  margin-bottom: 24px;

  @media (min-width: $tablet-width) {
    text-align: start;
  }

  @media (min-width: $desktop-width) {
    font-size: 30px;
    line-height: 30px;
    text-align: center;
  }
}

.advantages__item {
  @media (min-width: $desktop-width) {
    width: 300px;
    padding-top: 360px;
  }
}

.advantages__item--mood {
  @media (min-width: $desktop-width) {
    background-image: url("/img/screen-mood.png");
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/screen-mood@2x.png");
    }
  }
}

.advantages__item--world {
  @media (min-width: $desktop-width) {
    background-image: url("/img/screen-world.png");
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/screen-world@2x.png");
    }
  }
}

.advantages__item--friend {
  @media (min-width: $desktop-width) {
    background-image: url("/img/screen-friend.png");
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/screen-friend@2x.png");
    }
  }
}

.advantages__container {
  @media (min-width: $desktop-width) {
    background-color: $background;
    padding-top: 59px;
    position: relative;
    top: -59px;
  }
}

.advantages__description {
  text-align: center;
  margin: 0;
  margin-bottom: 43px;

  @media (min-width: $tablet-width) {
    margin-bottom: 47px;
    text-align: start;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 30px;
    text-align: center;
    width: 252px;
    margin: 0 auto;
  }
}
