.store__wrapper {
  display: grid;
  position: relative;
  z-index: 10;

  @media (min-width: $tablet-width) {
    margin-top: 74px;
    grid-template-columns: 50% 50%;
  }

  @media (min-width: $tablet-width) {
    margin-top: 98px;
  }
}

.store__title {
  width: 240px;
  font-size: 24px;
  line-height: 29px;
  font-weight: lighter;
  text-align: center;
  margin: 0 auto;
  margin-top: 29px;
  margin-bottom: 34px;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    line-height: 48px;
    position: relative;
    top: 30px;
    padding-top: 0;
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    font-size: 50px;
    line-height: 68.09px;
    margin-top: 0;
    margin-bottom: 0;
  }
}

.store__img {
  background-image: url("/img/store__img.png");
  background-repeat: no-repeat;
  min-width: 320px;
  min-height: 286px;
  position: relative;
  z-index: -10;
  background-size: auto 100%;
  background-position: 50% 100%;
  left: -70px;

  @media (min-width: $tablet-width) {
    background-size: auto 100%;
    background-position: 100% 100%;
    min-width: 610px;
    min-height: 545px;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
    left: -150px;

    @media (min-resolution: 2dppx) {
      background-image: url("/img/store__img@2x.png");
    }
  }

  @media (min-width: $desktop-width) {
    background-size: auto 100%;
    background-position: 50% 100%;
  }
}

.store__container {
  background-color: $basic;

  @media (min-width: $tablet-width) {
    background-color: transparent;
  }
}

.store__download {
  padding-top: 28px;
  padding-bottom: 38px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.store__button {
  width: 280px;
  margin: 0 auto;
  display: block;
  font-size: 18px;
  line-height: 18px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  color: inherit;
  border: 3px solid $basic-light;
  padding: 21px 0;
  border-radius: 31px;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    width: 300px;
  }
}

.store__list {
  width: 280px;
  margin: 0 auto;
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
  margin-top: 38px;
  margin-bottom: 31px;

  @media (min-width: $tablet-width) {
    width: 300px;
  }
}

.store__link--apple {
  display: block;
  width: 47px;
  height: 56px;
  background-image: url("/img/store__link--apple.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.store__link--android {
  display: block;
  width: 47px;
  height: 56px;
  background-image: url("/img/store__link--android.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.store__link--microsoft {
  display: block;
  width: 47px;
  height: 56px;
  background-image: url("/img/store__link--microsoft.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.store__footer {
  font-weight: lighter;
  text-align: center;
  margin: 0;
}
